/*transition animate*/

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0;
}
body, p, h1, h2, h3, h4, h5, h6, ul, li, ol{
    margin: 0;
    padding: 0;
}
body{
    font-family: '-apple-system,"Helvetica Neue",Arial,"PingFang SC","Hiragino Sans GB",STHeiti,"Microsoft YaHei","Microsoft JhengHei","Source Han Sans SC","Noto Sans CJK SC","Source Han Sans CN","Noto Sans SC","Source Han Sans TC","Noto Sans CJK TC","WenQuanYi Micro Hei",SimSun,sans-serif';
}
h1,h2,h3{
    font-weight: 800;
}
ul, li {
    list-style-type: none;
}
.title{
    text-align: center;
    font-weight: 800;
    margin: 20px 0;
}
summary{
    font-style: italic;
    color:#666;
    text-indent:2em;
}
h3{
    margin: 10px 0;
}
pre{
    margin: 15px 0;
    line-height: 1;
    background-color: #f7f7f7;
    border-radius: 5px;
    font-size: 85%;
    line-height: 1.45;
    font-family: Consolas,"Liberation Mono",Menlo,Courier,monospace;
}
.tag, .path, .id{
    display: none;
}
.warnning{
    color:#e4393c;
    font-weight: 800;
}
/********************************巨 幕begin************************************/
.jumbotron.jumbotron-wy{
  position: relative;
  /*height:420px;*/
  background:url(../images/bg.jpg) no-repeat 0 0;
  background-size: 100% 100%;
  text-align: center;
}
.jumbotron.jumbotron-wy .container{
  position: absolute;
  top:150px;
  left:0;
  right:0;
  color:#FFF;
}
/********************************巨 幕end************************************/
@media screen and (min-width:992px) {
    #nav a{
        font-size: 30px;
        cursor: pointer;
    }
    .jumbotron.jumbotron-wy{
        height:420px;
    }
    .jumbotron.jumbotron-wy .container{
        top:143px;
    }
    .title{
        font-size: 30px;
    }
    h3{
        font-size: 26px;
    }
    .shadow{
        padding:10px 50px;
    }
}
@media screen and (max-width:991px) and (min-width:768px) {
    .jumbotron.jumbotron-wy{
        height:360px;
    }
    .jumbotron.jumbotron-wy .container{
        top:123px;
    }
    .title{
        font-size: 26px;
    }
    h3{
        font-size: 22px;
    }
    .shadow{
        padding:10px 30px;
    }
}
@media screen and (max-width: 767px) {
    .jumbotron.jumbotron-wy{
        height: 300px;
    }
    .jumbotron.jumbotron-wy .container{
        top:93px;
    }
    .title{
        font-size: 22px;
    }
    h3{
        font-size: 18px;
    }
    .shadow{
        padding:10px;
    }
}



