
.backToTop{
    position: fixed;
    bottom: 25px;
    right:50px;
    height:50px;
    width:50px;
    background-color: #dfdfdf;
}
.backToTop a{
    position: relative;
    display: block;
    width:50px;
    height:50px;
}
.backToTop .white{
    position: absolute;
    top:-15px;
    border:25px solid;
    border-color: transparent transparent #FFF transparent;
}
.backToTop .gray{
    position: absolute;
    top:-10px;
    border:25px solid;
    border-color:  transparent transparent #dfdfdf transparent;
}



